	*{
		margin: 0;
		padding: 0;
	}
	
@import url('https://fonts.googleapis.com/css?family=Lato:300i,400,400i,700i,900&subset=latin-ext');
// font-family: 'Lato', sans-serif;
.wrap{
	padding: 25px 10px 50px 10px;
	// padding-top: 25px;
	// padding-bottom: 50px;
	// padding-right: 10px;

	min-width: 320px;
	font-family: Lato,Helvetica Neue,Helvetica,sans-serif;
	color: hsla(0, 0%, 0%, 0.75);
	background-color: rgba(0, 0, 0, 0.795);

}
.container{
	margin: 0 auto;
	max-width: 680px;
	padding-right: 5%;
	padding-left: 5%;
	line-height: 1.67;
	font-family: 'Lato', sans-serif;
	padding-bottom: 25px;
	padding-top: 7px;
	text-shadow: 0 1px 0 #fff;
}
.function{
	text-align: center;
	padding-top: 15px;
	font-size: 27px;
}
.header{
	margin-top: 15px;
	margin-bottom:15px;
	display: inline-block;
	display: flex;
	align-items: flex-start;
}
.contact__item{
	list-style-type: none;
	text-align: center;
	white-space: nowrap
}
.contact{
	width: 65%;	
	margin-right: auto;
	margin-left: auto;
}
.contact__list{
	font-size: 16px;
}
.myfoto{
	width: 30%;
	height: auto;
	box-shadow: 0 0 10px rgba(90, 90, 90, 0.432),
	inset 0 0 10px rgba(90, 90, 90, 0.432);
	border-radius: 4px;
}
.contact span{
	font-weight: 700;
	font-size: 15px;
	margin-right: 5px;

}
.contact__name{
	// margin-top: 5px;
	font-size: 18px;
	text-align: center;
	// margin-bottom: 5px;
}
@media screen and (max-width:600px){
	.myfoto{
		width: 40%;
	}
	.contact__list{
		// width: 55%;
		margin-top: 0px;
	}
}
@media screen and (max-width:450px){
	.header{
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.contact__list{
		width: 100%;
		margin-top: 10px;
	}
	.myfoto{
		width: 150px;
	}

}
.main{
	position: relative;
	margin: 0 auto;
	padding-bottom: 25px;
	overflow: hidden;

}
.main__head{
	font-size: 18px;
	margin-top: 5px;
	margin-left: 20px;

}
.main__head-4{
	font-size: 16px;
}
.main__text{
	font-style: italic;
	text-align: justify;
}
.main__text li{
	margin-left: 20px;
}
.main__item{
	margin-left: 15px;
}
.date, .bold{
	font-weight: bold;
}


.paper {
	margin: 15px auto;
	background: #fffffd;
	box-shadow:inset 0 0 1px rgba(0, 0, 0, 0.5),
	3px 3px 8px rgba(0, 0, 0, 0.4);
  }
.src{
	width: 18%;
	display: block;
	position: absolute;
	right: 0px;
	bottom: 0px;
	display: none;
	&:hover .src__qr{
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		transition: 0.5s ease-in-out;
	}
	&:hover .src__text{
		color:#007CC2;
		transition: 0.5s ease-in-out;

	}

  }
.src__qr{
	width:100%;
	height: auto;
	border-radius: 5px;
	transition: 0.5s ease-in-out;
	&:active{
		box-shadow: 0 0 20px rgba(0, 0, 0, 0.9);
		transition: 0.15s ease;

	}

}
.src__text{
	width: 100%;
	font-size: 14px;
	display: flex;
	justify-content: space-around;
	font-style: italic;
	font-weight: 700;
	position: relative;
	bottom: 12px;
	color: rgba(0, 0, 0, 0.4);
	transition: 0.3s ease-in-out;
	visibility: inherit;
}
.panelButton{
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	font-size: 0px;
}
.btn{
	background-color: rgba(255, 255, 255, 0);
	border: none;
	max-width: 150px;
	// padding:5px;
}
.panelButton .btn:nth-child(2){
	padding-left: 5px;
	padding-right: 5px;
}
.icon{
	fill:rgb(197, 197, 197);
	width: 100%;
	padding: 12px 15px;
}

.resumeButton{
	height: 80px;
	display: block;
	letter-spacing: 1px;
	border-radius: 4px;
	background-color: #444;
	color:#fff;
	font-weight: 600;
	font-size: 16px;
	cursor: pointer;
	border: 1px solid rgba(255, 255, 255, 0.2);
	transition: fill 0.3s, text-shadow 0.15s,background-color 0.3s , 0.5s box-shadow, border 1.5s ;
	outline: none;
	user-select: none;
	text-decoration: none;
	box-sizing: border-box;
	text-align: center;
	text-shadow: 0 0 4px rgba(255, 255, 255, 0);


	&:hover, &:focus{
		background-color:  #444 * 0.8;
		box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
		border: 1px solid rgba(255, 255, 255, 0.5);
		text-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
		fill:#fff;
		transition: fill 0.15s, text-shadow 0.15s, background-color 0.3s, box-shadow 1s, border 0.15s;
	}
	&:active{
		box-shadow:inset 0 0 15px rgba(255, 255, 255, 0.8),
		0 0 10px rgba(255, 255, 255, 0.3);
		text-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
		transition: text-shadow 0.15s, background-color 0.3s, box-shadow 1s, border 0.15s;
	}
}
@media screen and (max-width:550px){
	.src{
		position: static;
		margin: 0 auto;
		margin-top: 15px;
	}
	.src__text{
		font-size: 14px;
	}
	.btn{
		max-width: initial;
	}
	.panelButton.container{
		padding-right: initial;
		padding-left: initial;
	}
	.resumeButton{
		height: 60px;
	}

}
@media print /* screen */{
	.myfoto{
		width: 165px;
	}
	.main__head{
		margin-top: 0px;
	
	}
	.src{
		display: block;
	}
	body * {
		visibility: hidden;
	  }
	
	  .container *{
		visibility: visible;
	  }
	
	.container  {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		max-width: 700px;
		padding-bottom: 15px;
		padding-top: 15px;

	}
	.panelButton{
		display: none;
	}
}
